.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
.dvs-fieldset label {
@apply dvs-block dvs-mb-2 dvs-text-xs dvs-uppercase dvs-font-bold dvs-tracking-superwide;
}
.dvs-fieldset label.dvs-large-label {
@apply dvs-text-base dvs-font-normal dvs-normal-case dvs-tracking-normal;
}
.dvs-fieldset label.dvs-small-label {
@apply dvs-text-sm dvs-font-normal dvs-normal-case dvs-tracking-normal;
}
.dvs-fieldset textarea,
.dvs-fieldset select,
.dvs-fieldset input[type="text"],
.dvs-fieldset input[type="password"],
.dvs-fieldset input[type="date"],
.dvs-fieldset input[type="datetime-local"],
.dvs-fieldset input[type="number"],
.dvs-fieldset input[type="email"],
.dvs-fieldset input[type="phone"] {
@apply dvs-p-4 dvs-w-full dvs-bg-grey-lighter dvs-rounded-sm dvs-appearance-none;
}
.dvs-fieldset textarea.dvs-w-auto,
  .dvs-fieldset select.dvs-w-auto,
  .dvs-fieldset input[type="text"].dvs-w-auto,
  .dvs-fieldset input[type="password"].dvs-w-auto,
  .dvs-fieldset input[type="date"].dvs-w-auto,
  .dvs-fieldset input[type="datetime-local"].dvs-w-auto,
  .dvs-fieldset input[type="number"].dvs-w-auto,
  .dvs-fieldset input[type="email"].dvs-w-auto,
  .dvs-fieldset input[type="phone"].dvs-w-auto {
@apply dvs-w-auto;
}
.dvs-toggle {
@apply dvs-inline-block dvs-relative;
  height: 34px;
  width: 60px;
}
.dvs-toggle input {
@apply dvs-hidden;
}
.dvs-toggle input:checked + .dvs-toggle-slider {
      background-color: #66bb6a;
}
.dvs-toggle input:checked + .dvs-toggle-slider:before {
        -webkit-transform: translateX(26px);
                transform: translateX(26px);
}
.dvs-toggle .dvs-toggle-slider {
@apply dvs-cursor-pointer dvs-absolute dvs-pin;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;
}
.dvs-toggle .dvs-toggle-slider:before {
@apply dvs-absolute;
      content: "";
      background-color: #fff;
      bottom: 4px;
      left: 4px;
      height: 26px;
      width: 26px;
      border-radius: 50%;
      -webkit-transition: .4s;
      transition: .4s;
}
.help {
@apply dvs-rounded dvs-p-8 dvs-bg-grey-lightest dvs-text-sm dvs-border-l-4 dvs-border-blue;
}
.help.danger {
@apply dvs-bg-red dvs-text-white;
}
.help p {
@apply dvs-text-blue;
}
.dvs-collapsable .dvs-switch-sm,
.dvs-collapsable .dvs-switch {
@apply dvs-rounded-sm dvs-bg-grey-light dvs-font-bold dvs-p-4 dvs-cursor-pointer dvs-opacity-75;
}
.dvs-collapsable .dvs-switch-sm {
@apply dvs-py-3 dvs-text-sm;
}
.dvs-collapsable > .dvs-collapsed {
